<template>
  <div class="soundButton" @click="makeSound">
    <!-- <font-awesome-icon icon="circle-left" beat /> -->
    <img class="backButtonImg" src="@/assets/img/common/volume.png" />

    <VueSpeechSynthesis
      ref="audioOut"
      :text="CaptionText"
      lang="en-US"
      :pitch="1"
      :volume="1"
      :rate="0.7"
      :read-on-load="true"
      :read-on-change="true"
    />
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import VueSpeechSynthesis from "@/components/VueSpeechSynthesis.vue";
const audioOut = ref(VueSpeechSynthesis);

export default defineComponent({
  setup() {
    return {};
  },
  props: {
    CaptionText: {
      type: String,
      required: true,
    },
    LinkSound: String,
  },
  methods: {
    makeSound() {
      //alert(this.Caption);
      (this.$refs.audioOut as typeof VueSpeechSynthesis).start();
      //this.$refs.audioOut.start();
      //audioOut.value.repeat();
    },
  },
  watch: {
    CaptionText() {
      if (
        (this.$refs.audioOut as typeof VueSpeechSynthesis).$data.isSpeaking ===
        true
      ) {
        (this.$refs.audioOut as typeof VueSpeechSynthesis).stop();
      }

      // (this.$refs.audioOut as typeof VueSpeechSynthesis).stop();
    },
  },
  components: {
    VueSpeechSynthesis,
  },
});
</script>

