import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import HomeView from "../views/HomeView.vue";
import HelloView from "../views/HelloView.vue";
import DictionarySelPic from "@/components/DictionarySelectPicEngine.vue";
import DictionarySelCloudPic from "@/components/DictionarySelectPicCloudEngine.vue";
import DictionarySelectPicWheelEngine from "@/components/DictionarySelectPicWheelEngine.vue";
import SimpleDictionary from "@/components/DictionarySimpleEngine.vue";
import ColorView from "@/views/ColorView.vue";
import Settings from "@/views/SettingsView.vue"

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/hello",
    name: "hello",
    component: HelloView,
  },
  {
    path: "/ColorView",
    name: "ColorView",
    component: ColorView,
  },
  {
    path: "/dictSPic/:dictname",
    name: "dictSPic",
    component: DictionarySelPic,
  },
  {
    path: "/settings",
    name: "settings",
    component: Settings,
  },
  {
    path: "/simpleDict/:dictname",
    name: "simpleDict",
   
    component: SimpleDictionary,
  },
  {
    path: "/dictSCloudPic/:dictname",
    name: "dictSCloudPic",
   
    component: DictionarySelCloudPic,
  },
  {
    path: "/dictSWheelPic/:dictname",
    name: "dictSWheelPic",
   
    component: DictionarySelectPicWheelEngine,
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
