<template>
  <div
    @click="makeSound"
    class="card--sm--img w-32 h-32 sm:w-48 sm:h-48 md:w-36 md:h-36 lg:w-64 lg:h-64 xl:w-60 xl:h-60 items-center"
    :class="{
      'card--correct': IsCorrect && isSelected,
      'card--wrong': !IsCorrect && isSelected,
      // 'shake':!IsCorrect&&isSelected,
      pulse: IsCorrect && isSelected,
    }"
    :style="{
      'background-color': BGColor ? BGColor : 'white',
      'background-image': 'url(' + Img + ')',
    }"
  >
    <h1 class="text-7xl self-center">{{ Answ }}</h1>
    <VueSpeechSynthesis
      ref="audioOut"
      :text="Answ ?? Caption"
      lang="en-US"
      :pitch="1"
      :volume="1"
      :rate="0.7"
      :read-on-load="false"
      :read-on-change="false"
    />
    <!-- <p>IMG:{{ Img }}</p> -->
    <!-- <img :src="Img"/> -->
    <audio hidden="true" ref="audioW" src="/sound/common/wrong2.wav" />
    <audio hidden="true" ref="audioS" src="/sound/common/success.wav" />
  </div>
</template>

<script lang="ts">
import SoundButton from "@/UI/SoundButton.vue";
import SoundNativeButton from "@/UI/SoundNativeButton.vue";
import { defineComponent, ref, onMounted } from "vue";
import VueSpeechSynthesis from "@/components/VueSpeechSynthesis.vue";

const audioOut = ref(VueSpeechSynthesis);

export default defineComponent({
  setup() {
    const audioW = ref<HTMLAudioElement>();
    const audioS = ref<HTMLAudioElement>();
    return { audioW, audioS };
  },
  data() {
    return {
      nativeCaption: "",
      isSelected: false,
    };
  },
  methods: {
    makeSound() {
      if (this.IsCorrect) {
        //setTimeout(() => this.audioS?.play(), 10);
        this.audioS?.play();
      } else {
        //setTimeout(() => this.audioW?.play(), 10);
        this.audioW?.play();
      }

      this.isSelected = true;
      (this.$refs.audioOut as typeof VueSpeechSynthesis).start();
    },
  },

  computed: {
    getNative() {
      //console.log("getNative");
      var locLang = "ru-RU";
      if (this.Translations) {
        //console.log(this.Translations);
        var castedArr = <ITranslations[]>this.Translations;
        // console.log(castedArr);
        var foundTransl = castedArr.filter((a) => a.lang == locLang);
        // console.log(foundTransl[0].name);

        return foundTransl[0].name;
      } else {
        return "";
      }
    },
  },
  props: {
    Caption: {
      type: String,
      required: true,
    },
    Translations: {
      type: Array,
      required: false,
    },

    BGColor: {
      type: String,
      required: false,
    },
    Img: {
      type: String,
      required: false,
    },
    IsCorrect: {
      type: Boolean,
      required: false,
      default: false,
    },
    Answ: {
      type: String,
      required: false,
    },
  },
  components: { SoundButton, SoundNativeButton, VueSpeechSynthesis },
});
</script>

<style scoped>
.card--sm--img {
  /* border: 2px violet solid; */
  /* transform: translateY(-5px); */
  /* filter: drop-shadow(15px 15px 15px #a809cc); */
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: flex;
  justify-content: center;
  align-items: end;
  /* width: 200px;
  height: 200px; */
  margin: 20px;
}
.card--correct {
  border: 5px rgb(11, 182, 76) solid;
  filter: drop-shadow(15px 15px 15px rgb(11, 182, 76));
}
.card--wrong {
  border: 5px rgb(182, 11, 11) solid;
  filter: drop-shadow(15px 15px 15px rgb(182, 11, 11));
  opacity: 0.3;
}

.card--sm--img img {
  object-fit: cover;
  object-position: center;
}
h2 {
  @apply text-3xl font-bold;
}

@keyframes min-round_round {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}
</style>
