<template>
   
</template>

 

<script lang="ts">
import { defineComponent } from "vue";

const sSpeech = window.speechSynthesis;

export default defineComponent({
  setup() {
    return {};
  },
  expose:['start','stop'],
  data() {
    return {
      isSpeaking: false,
      synth: window.speechSynthesis,
    };
  },
  props: {
    text: {
      type: [String, Array],
      required: true,
    },
    readOnLoad: {
      type: Boolean,
      default: false,
    },
    readOnChange: {
      type: Boolean,
      default: false,
    },
    lang:  {
      type: String,
      default: "en-Us",
    },
    pitch: {
      type: Number,
      default: 1,
    },
    volume: {
      type: Number,
      default: 1,
    },
    rate: {
      type: Number,
      default: 1,
    },
  },
  computed:{
    utteranceText() {
      
      return Array.isArray(this.text) ? this.text.join(" ") : this.text;
    },

  },
  methods: {
    stop() {
      this.synth.cancel();
      this.isSpeaking = false;
      this.$emit("stop");
    },
    start() {

    //     if (this.isSpeaking 
    //   //|| this.$_synth.speaking
    //   ) {
    //     this.stop();
    //     return;
    //   }

      if (this.utteranceText === "") {
        return;
      }

      const utterance = new SpeechSynthesisUtterance(this.utteranceText);
      utterance.volume = this.volume;
      utterance.rate = this.rate;
      utterance.pitch = this.pitch;
      utterance.lang = this.lang;
       
      //console.log(this.synth.getVoices());

      utterance.addEventListener("end", (e) => {
        // if (this.$_synth.pending || this.$_synth.speaking) {
        //   return;
        // }

        this.isSpeaking = false;
        this.$emit("end", e);
      });

      this.synth.speak(utterance);
      this.isSpeaking = true;
      this.$emit("start");

    },
  },
  watch: {
    text() {
     
      if (this.readOnChange) {
        this.start();
      }
    },
  },
  created() {
    if (!sSpeech) {
      alert("Speech Synthesis is not supported in current browser.");
      throw new Error("Speech Synthesis is not supported in current browser.");
    }
    // const speechSynthesis = window.speechSynthesis;
    // if (!speechSynthesis && process.env.NODE_ENV !== "production") {
    //   throw new Error("Speech Synthesis is not supported in current browser.");
    // }
    // this.synth = speechSynthesis;
  },
  mounted() {
    if (this.readOnLoad) {
      this.start();
    }
  },
});
</script>

