<template>
  <div class="scene">
    <div
      class="flex flex-row justify-between items-center"
      style="height: 10vh"
    >
      <BackButton />
      <div class="flex flex-row cent">
        <h1>{{ currDict.name }}</h1>
      </div>
      <p></p>
    </div>
    <div class="flex flex-col justify-between" style="min-height: 90vh">
      <div
        :key="currObj.id"
        class="circle self-center mt-28 place-self-center"
        :style="{ '--total': currAnswers.length, '--curr-angle': 0 }"
        :class="{ spin0: !isSpin, 'rot--div': isSpin }"
      >
        <div
          class="quest"
          :class="{ spin0: !isSpin, ' rot--div--min': isSpin }"
        >
          <h1>
            {{ currObj.name }}
          </h1>
          <div class="flex flex-row">
            <SoundButton :CaptionText="currObj.name" LinkSound="" />
          <SoundNativeButton
            v-if="currObj?.translates"
            :CaptionText="getNative"
            LinkSound=""
          />
          </div>
         
        </div>
        <div
          v-for="(item, key, index) in currAnswers"
          class="stat"
          :key="item.id"
          :style="{ '--i': key + 1, '--rotTurn': rotTurn + 'turn' }"
        >
          <CardItemImg
            :key="item.id"
            :Caption="item.name"
            :BGColor="item.bg"
            :Translations="item.translates"
            :Img="item.image"
            :IsCorrect="item.name === currObj.name"
            @click="cardClick(item.name === currObj.name)"
            style="
              width: 20vmin;
              height: 20vmin;
              border-radius: 50%;
              margin: 0px;
            "
            :class="{ spin0: !isSpin, 'rot--div--min': isSpin }"
          />
        </div>
      </div>

      <h2 v-if="isSolved">Good JOB</h2>
      <h2 v-else>Waiting for solve...</h2>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import { useSound } from "@vueuse/sound";
//import wrSound from "@/assets/sounds/wrong1.wav"
// import {cDicts} from '@/data/colorsCards.js'
//import json from "../data/colorsCards.json";
import CardItemImg from "@/components/CardItemSelImg.vue";
import SwitchDictButton from "@/UI/SwitchDictButton.vue";
import BackButton from "@/UI/BackButton.vue";
import SoundButton from "@/UI/SoundButton.vue";
import SoundNativeButton from "@/UI/SoundNativeButton.vue";
//import { Howl, Howler } from "howler";

export default defineComponent({
  setup() {
    // import("@/data/sdict/" + "colorsCards.json")
    //   .then(module=>{console.log(module.default)});
    const wrongSound = useSound("/sound/common/wrong3.waw", { volume: 1 });
    const goodSound = useSound("sound/common/success2.waw", { volume: 1 });
    const audioW = ref<HTMLAudioElement>();
    const audioS = ref<HTMLAudioElement>();
    // var sound = new Howl({
    //   src: ["/sound/common/wrong1.waw"],
    // });

    // sound.play();
    //wrongSound.play();
    return { wrongSound, goodSound, audioW, audioS };
  },
  data() {
    return {
      currObj: {} as IDictionaryItem,
      currAnswers: [] as IDictionaryItem[],
      jdata: [] as IDictionaryItem[],
      currDict: {} as IDictionary,
      count: 0,
      isSolved: false,
      oldval: -100,
      rotTurn: 0.0,
      isSpin: false,
    };
  },
  computed: {
    getNative() {
      var locLang = "ru-RU";
      console.log(this.currObj as IDictionaryItem);

      var translates = <ITranslations[]>this.currObj.translates;

      if (this.currObj?.translates) {
        //console.log(this.currObj.translates);
        var castedArr = <ITranslations[]>this.currObj.translates;
        //console.log(castedArr);
        var foundTransl = castedArr.filter((a) => a.lang === locLang);
        //console.log(foundTransl[0]);
        return foundTransl[0]?.name;
      } else {
        return "";
      }

      return "";
    },
    getNextTask(): string {
      var cardsCount = this.jdata.length;
      //console.log(cardsCount);
      var randVal = Math.floor(Math.random() * cardsCount);
      if (this.jdata.length > 0) {
        var qElement = this.jdata.length;

        //console.log(this.count);
        //console.log(randVal);
        return qElement.toString();
      } else {
        return "-1";
      }
    },
  },
  async created() {
    import("@/data/sdict/" + this.$route.params.dictname + ".json").then(
      (module) => {
        this.currDict = module.default;
        this.jdata = module.default.items;

        this.currObj = this.jdata[0];
        this.moveNext();
        const self = this;
        // setInterval(function () {
        //   self.rotate();
        // }, 4000);
      }
    );
  },
  components: {
    CardItemImg,
    SwitchDictButton,
    BackButton,
    SoundButton,
    SoundNativeButton,
  },
  methods: {
    moveBack() {
      if (this.count === 0) {
        this.count = this.jdata.length - 1;
      } else {
        this.count = this.count - 1;
      }
      this.currObj = this.jdata[this.count];
    },
    rotate() {
      //this.rotTurn =  this.rotTurn-0.25;
    },
    cardClick(e: boolean) {
      if (e) {
        this.isSolved = true;

        setTimeout(() => this.moveNext(), 2000);
      } else {
      }
    },
    moveNext() {
      // if (this.count === this.jdata.length - 1) {
      //   this.count = 0;
      // } else {
      //   this.count = this.count + 1;
      // }
      const maxAnsw = 5;

      this.isSpin = false;
      const self = this;
      setTimeout(function () {
        self.isSpin = true;
      }, 10);

      // this.rotTurn =this.rotTurn+ 0.1;

      var randVal = Math.floor(Math.random() * this.jdata.length);

      while (randVal === this.oldval) {
        randVal = Math.floor(Math.random() * this.jdata.length);
      }

      this.oldval = randVal;
      this.currObj = this.jdata[randVal];
      var answers = [];
      answers.push(this.currObj);

      while (
        answers.length !=
        (this.jdata.length > maxAnsw ? maxAnsw : this.jdata.length)
      ) {
        var answerVal = Math.floor(Math.random() * this.jdata.length);

        var newCand = this.jdata[answerVal];

        if (answers.filter((x) => x.name === newCand.name).length === 0) {
          answers.push(newCand);
        } else {
          continue;
        }
        //console.log(answers.length);
      }

      //We need map to force refresh same cards
      this.currAnswers = answers
        .sort(function () {
          return 0.5 - Math.random();
        })
        .map((item) => {
          return { ...item, id: item.id + Math.floor(Math.random() * 100000) };
        });
      this.isSolved = false;
    },
  },
});
</script>

<style scoped>
.scene {
  /* display: flex;
  height: 100vh; */
  background: rgb(238, 174, 202);
  background: radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
  /* background-color: gray; */
}

.circle {
  display: grid;
  grid-template-areas: "layer";
  place-items: center;
  background: rgb(252, 70, 107);
  background: radial-gradient(
    circle,
    rgba(252, 70, 107, 0) 0%,
    rgb(198, 63, 251) 100%
  );
  border-radius: 50%;

  --radius: 25vmin;
  width: calc(2 * var(--radius));
  height: calc(2 * var(--radius));
}

.quest {
  grid-area: layer;
  width: 20vmin;
  height: 20vmin;
  border-radius: 50%;

  display: grid;
  place-items: center;

  background: transparent;
  /* color: #185adb; */
  font-weight: bold;
  font-size: 3vmin;

  /* Decimal value, 0-1 based on the child's index. */
  --d: calc(var(--i) / var(--total));
  /* // Offset to get better starting placement on the circle */
  --r-offset: -0.25turn;
  /* // Full circle */
  --r-amount: 1turn;
  /* // Rotation based on the decimal and r modifiers */
  --r: calc((var(--r-amount) * var(--d)) + var(--r-offset));
  /* // Rotate, transform out, then negative rotation so the content appears upright */
  --transform: rotate(var(--r)) translate(var(--radius))
    rotate(calc(-1 * var(--r)));

  transform: var(--transform);
  transition: transform 4s;
  /* //transition-delay: calc(0.1s * var(--i)); */

  .circle:hover & {
    /* --radius: calc(-20vmin); */
    /* // Animate the rotation */
    /* --r-offset: -1.75turn; */
  }
}

.stat {
  grid-area: layer;
  width: 20vmin;
  height: 20vmin;
  border-radius: 50%;

  display: grid;
  place-items: center;

  /* background: #ffc947; */
  color: #185adb;
  font-weight: bold;
  font-size: 3vmin;

  /* Decimal value, 0-1 based on the child's index. */
  --d: calc(var(--i) / var(--total));
  /* // Offset to get better starting placement on the circle */
  --r-offset: calc(-1 * var(--rotTurn));
  /* // Full circle */
  --r-amount: 1turn;
  /* // Rotation based on the decimal and r modifiers */
  --r: calc((var(--r-amount) * var(--d)) + var(--r-offset));
  /* // Rotate, transform out, then negative rotation so the content appears upright */
  --transform: rotate(var(--r)) translate(var(--radius))
    rotate(calc(-1 * var(--r)));

  transform: var(--transform);
  transition: transform 3.5s ease-in-out;

  /* //transition-delay: calc(0.1s * var(--i)); */

  .circle:hover & {
    /* --radius: calc(-20vmin); */
    /* // Animate the rotation */
    /* --r-offset: -1.75turn; */
  }
}

img {
  max-width: 40%;
}

@keyframes round_round {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes min-round_round {
  from {
    transform: rotate(--curr-angle);
  }
  to {
    transform: rotate(-360deg);
  }
}

.rot--div {
  /* width: 50px;
    height: 50px; */
  animation: round_round 10s linear infinite;
  /* margin: 50px auto 0;
    transform-origin: 50% 150px;
    background-color: #8FC1E0; */
}

.spin0 {
  /* width: 50px;
    height: 50px; */
  rotate: 0;
  /* margin: 50px auto 0;
    transform-origin: 50% 150px;
    background-color: #8FC1E0; */
}

.rot--div--min {
  /* width: 50px;
    height: 50px; */

  animation: min-round_round 10s linear infinite;
  /* margin: 50px auto 0;
    transform-origin: 50% 150px;
    background-color: #8FC1E0; */
}
</style>
