<template>
  <div>
    <div class="flex flex-row justify-between items-center" style="height: 10vh">
      <BackButton />
      <div class="flex flex-row">
        <h1>{{currDict.name}}</h1>
        <!-- <SoundButton :CaptionText="currDict.name" LinkSound="" />
         
        <SoundNativeButton :CaptionText="getNative" LinkSound="" />   -->
      </div>
      <p></p>
    </div>
    <div class="flex flex-row" style="min-height: 90vh">
      
      <div class="basis-1/4 flex content-center justify-center bg-slate-300">
        <SwitchDictButton @click="moveBack">BACK</SwitchDictButton>
      </div>
      <div class="basis-1/2">
        <CardItem
          :key="currObj.id"
          :Caption="currObj.name"
          :BGColor="currObj.bg"
          :Translations="currObj.translates"
          :Img="currObj.image"
          v-touch:swipe.left="moveNext"
          v-touch:swipe.right="moveBack"
          
        />
      </div>
      <div class="basis-1/4 flex content-center justify-center bg-slate-300">
        <SwitchDictButton @click="moveNext" :isBack="false"
          >NEXT</SwitchDictButton
        >
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
// import {cDicts} from '@/data/colorsCards.js'
//import json from "../data/colorsCards.json";
import CardItem from "./CardItem.vue";
import SwitchDictButton from "@/UI/SwitchDictButton.vue";
import BackButton from "@/UI/BackButton.vue";
import SoundButton from "@/UI/SoundButton.vue";
import SoundNativeButton from "@/UI/SoundNativeButton.vue";

export default defineComponent({
  setup() {
    // import("@/data/sdict/" + "colorsCards.json")
    //   .then(module=>{console.log(module.default)});
    return {};
  },
  data() {
    return {
      currObj: {} as IDictionaryItem,
      jdata: [] as IDictionaryItem[],
      currDict: {} as IDictionary,
      count: 0,
    };
  },
  computed: {
    getNative() {
      //console.log("getNative");
      var locLang = "ru-RU";
      if (this.currDict.Translations) {
        //console.log(this.Translations);
         var castedArr = <ITranslations[]>this.currDict.Translations;
        // console.log(castedArr);
         var foundTransl = castedArr.filter((a) => a.lang == locLang);
        // console.log(foundTransl[0].name);

         return foundTransl[0].name;
      } else {
        return "";
      }
    }
  },
  async created() {
    // this.jdata = Array<IDictionary>(
    //   await import("@/data/sdict/" + "colorsCards.json")
    // );
    // import("@/data/sdict/" + "colorsCards.json")
    //   .then(module=>{console.log(Array<IDictionary>(module.default)) });
    //console.log(this.$route.params);
    import("@/data/sdict/" + this.$route.params.dictname + ".json").then((module) => {
      this.currDict=module.default;
      this.jdata = module.default.items;
      //console.log(this.jdata);
      //console.log(this.jdata[0]);
      this.currObj = this.jdata[0];
    });

    //console.log(this.jdata);
    //this.json = this.jdata;
    //this.currObj=this.jdata[0];
    //this.currObj=(Array<IDictionary>)this.jdata[0];
  },
  components: {
    CardItem,
    SwitchDictButton,
    BackButton,
    SoundButton,
    SoundNativeButton,
  },
  methods: {
    moveBack() {
      if (this.count === 0) {
        this.count = this.jdata.length - 1;
      } else {
        this.count = this.count - 1;
      }
      this.currObj = this.jdata[this.count];
      //console.log(this.currObj.translates);
    },
    moveNext() {
      if (this.count === this.jdata.length - 1) {
        this.count = 0;
      } else {
        this.count = this.count + 1;
      }
      this.currObj = this.jdata[this.count];
    } 
  },
});
</script>

<style scoped>
.scene {
  display: flex;
  height: 100vh;
  /* background-color: gray; */
}
</style>
